.container-left-primary{
    @include grid(2, 0rem);
    @include degraded-background($principal, $secundario);
    height: 100vh;
    h1{
        font-weight: bold;
        font-size: 5rem;
    }
    h3{
        font-size: 3rem;
        font-weight: lighter;
    }
}
.container-left-secondary{
    @include grid(2, 0rem);
    @include degraded-background($secundario, $terciario);
    height: 100vh;
    h1{
        font-weight: bold;
        font-size: 5rem;
    }
    h3{
        font-size: 3rem;
        font-weight: lighter;
    }
}
.container-left-secondary-register{
    @include grid(2, 0rem);
    @include degraded-background($secundario, $terciario);
    h1{
        font-weight: bold;
        font-size: 5rem;
    }
    h3{
        font-size: 3rem;
        font-weight: lighter;
    }
    .div-aux{
        padding: 2rem 0;;
    }
}
.window-login{
    text-align: center;
    margin: auto;
    width: 80%;
    border-radius: 2rem;
    background-color: $blanco;
    form{
        width: 80%;
        margin: auto;
        display: flex;
        flex-direction: column;
        label{
            text-align: left;
        }
        input{
            width: 100%;
            border-radius: 1rem;
            align-self: center;
            border-color: $terciario;
            margin-bottom: 2rem;
        }
    }
}

.logo-login{
    margin: auto;
    width: 25%;
}