.client-branchs{
    display: grid;
    grid-template-columns: 20% 50% 30%;
    gap: 2rem;
}

.title{
    .title-p{
        top: 50%;
        position: absolute;
        text-align: center;
        width: 20%;
    }
    background-color: $terciario;
    img{ 
        position: relative;
        height: 100vh;
        width: 100%;
        filter: opacity(60%);
    }
}

.map{
    height: 80vh;
    top: 10%;
    border-radius: 1rem;
    border-color: 3px $principal;
}

.location{
    text-align: center;
    margin: auto;
    form{
        display: flex;
        flex-direction: column;
        select{
            padding: 1rem 2rem;
            border-color: $terciario;
            border-radius: 1rem;
            color: black;
            text-align: center;
            font-weight: bold;
        }
        button{
            margin-top: 2rem;
        }
    }
}