.seleccion-sucursal{
    margin-top: 2rem;
    @include tablet{
        margin-top: 3rem;
    }
    h1{
        font-size: 5rem;
        @include desktop{
            font-size: 10rem;
        }
        @include tablet{
            font-size: 7rem;
        }
    }
    .sucursal-contenido{
        background-color: $color4;
        border-radius: 0.5rem;
        margin-bottom: 2rem;
        @include desktop{
            display: grid;
            grid-template-columns: repeat(2,1fr);
            grid-template-rows: 1fr 5fr;
        }
    }
    .sucursal-contenido:last-child{
        margin: 0;
        @include desktop{
            margin: 0;
        }
    }
    .nombre{
        background-color: $color5;
        border-radius: 0.5rem;
        margin: auto;
        padding: 1.5rem 10rem;
        h1{
            color: $color1;
            margin: 0;
            @include tablet{
                font-size: 2.7rem;
            }
            @include desktop{
                font-size: 2.7rem;
            }
            font-size: 2.4rem;
        }
    }
    .info{
        margin: 0rem 2rem;
        span{
            color: $color3;
        }
        form{
            display: flex;
            justify-content: flex-end;
        }
    }
    .mapa{
        height: 30rem;
        @include tablet{
            height: 40rem;
            grid-row: 1 / 3;
            grid-column: 2 / 3;
        }
        @include desktop{
            height: 40rem;
            grid-row: 1 / 3;
            grid-column: 2 / 3;
            margin: 5% 5%;
        }
          
    }
}

.seleccion-cliente-encargada{
    margin-top: 10rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
}

.text-informativo{
    margin-top: 10rem;
    p{
        font-size: 2.4rem;
    }
}
.navegacion-inicio{
    border: none;
    padding: 1rem 0;
    background-color: $color5;
    @include tablet{
        display: grid;
        grid-template-columns: repeat(2,1fr);
    }
}

.seleccion-sucursal{
    display: block;
}
.seleccion-no-sucursal{
    display: none;
}

.nombre-usuario{
    margin: auto;
    font-size: 2.4rem;
    color: $color1;

}

.log-out{
    display: none;
}
.login{
    display: block;
}