.container {
    margin: auto;
    width: 80%;
}
.container-small {
    margin: auto;
    width: 90%;
    @include tablet{
        width: 75%;
    }
    @include desktop{
        width: 50%;
    }
}
.center{
    text-align: center;
}

.text-center {
    text-align: center;
}

.text-justify{
    text-align: justify;
    margin: 2rem;
}

.text-white{
    color: white;
}

.text-black{
    color: black;
}

.text-primary{
    color: $principal;
}

.width-100{
    width: 100%;
}

.formulario {
    form {
        width: 100%;
        padding: 2rem 2rem;
        display: flex;
        flex-direction: column;
    }
    label {
        margin-bottom: 15px;
        position: relative;
        border-bottom: 1px solid #ddd;
    }
    input {
        width: 100%;
        padding: 10px 0px;
        margin-top: 20px;
        border: none;
        border-radius: 0.5rem;
        outline: none;
    }
    select{
        width: 100%;
        padding: 10px 0px;
        margin-top: 20px;
        border: none;
        outline: none;
        border-radius: 0.5rem;
        text-align: center;
    }
    input::placeholder {
        opacity: 0;
        border-radius: 0.5rem;
    }
    span {
        position: absolute;
        top: 0;
        left: 0;
        transform: translateY(30px);
        font-size: 0.825em;
        transition-duration: 300ms;
    }
    label:focus-within>span,
    input:not(:placeholder-shown)+span{
       // color: $color5;
        transform: translateY(0px);
    }
    label:focus-within>span,
    select:not(:placeholder-shown)+span{
        //color: $color5;
        transform: translateY(0px);
    }
}

.btn-principal {
    font-weight: bold;
    border: none;
    background-color: $principal;
    border-radius: 0.5rem;
    padding: 1rem 2rem;
    color: $color1;
    margin-bottom: 2rem;
}

.btn-principal:hover {
    cursor: pointer;
    background-color: darken($principal, 8);
}

.btn-secundario {
    font-weight: bold;
    border: none;
    background-color: $blanco;
    border-radius: 0.5rem;
    padding: 0.5rem 0;
    color: $principal;
    padding: 1rem 2rem;
}

.btn-secundario:hover {
    cursor: pointer;
    background-color: darken($blanco, 8);
}
.btn-terciario {
    font-weight: bold;
    border: none;
    background-color: $gris;
    border-radius: 0.5rem;
    padding: 1rem 2rem;
    color: $principal;
    margin-bottom: 2rem;
}

.btn-terciario:hover {
    cursor: pointer;
    background-color: darken($gris, 8);
}

.error {
    background-color: lighten($rojo, 47);
    border-left: 5px solid $rojo;
    text-align: center;
    padding: 0.2rem 0;
    margin: 1rem 2rem;
    font-weight: $bold;
}

.successful {
    background-color: lighten($verde, 47);
    border-left: 5px solid $verde;
    padding: 0.2rem 0;
    text-align: center;
    margin: 1rem 2rem;
    font-weight: $bold;
}

.btn-volver,
.btn-cerrar-sesion{
    border: none;
    background-color: $gris;
    border-radius: 0.5rem;
    padding: 0.5rem 1rem;
}
.btn-volver:hover,
.btn-cerrar-sesion:hover {
    cursor: pointer;
    background-color: darken($gris, 10);
}

.div-btn-volver{
    margin: 1rem;
}

.animacion-menu{
    display: flex;
    align-items: center;
    img{
        filter: invert(1);
        width: 4rem;
    }
    @include tablet{
        margin-right: 5rem;
        display: flex;
        justify-content: flex-end;
        img{
            filter: invert(1);
            width: 4rem;
        }
    }
    @include desktop{
        margin-right: 5rem;
        display: flex;
        justify-content: flex-end;
        img{
            filter: invert(1);
            width: 4rem;
        }
    }
    .acciones{
        display: none;
    }
}
.animacion-menu:hover{
    cursor: pointer;
}
.mostrar{
    display: block;
}

.solo-icono{
    margin: 1rem 2rem 0rem 2rem;
    a{
        width: 5rem;
        img{
            width: 5rem;
        }
    }
    button{
        width: 5rem;
        background-color: transparent;
    }
}

.opciones-menu{
    margin-top: 2rem;
}

.opciones-menu:first-child{
    margin: 0;
}

.btn-navegacion-barra{
    background-color: transparent;
    border: none;
    width: 5rem;
    p{
        margin: 0;
        color: black;
        font-size: 1.5rem;
        font-weight: 600;  
    }
}
.btn-navegacion-barra:hover{
    cursor: pointer;
}

.login-img:hover{
    cursor: pointer;
}

.no-mostrar{
    display: none;
}
.ocultar{
    visibility: hidden;
}
.icon-back{
    margin: 2rem 2rem;
    width: 10%;
}

.icon-back:hover{
    cursor: pointer;
}

.btn-back{
    border: none;
    background-color: transparent;
}
.btn-back:hover{
    cursor: pointer;
}

.btn-duals{
    @include grid(2,1rem);
}

.btn-option{
    margin-bottom: 2rem;
}

.swal2-popup {
    background: #ffffff;
}

.swal2-title {
  color:#000000;
}

swal2-styled.swal2-confirm {
    background-color:#5d14a2
}